.no-todos {
    font-size: 1.6em;
}

.todo {
    display: flex;
    flex-direction: column;
    font-size: 1.5em;
    border-bottom: 1px solid #202020;
}

.todo:not(:first-of-type) {
    margin-top: 30px;
}

.todo-name {
    display: flex;
    align-items: center;
}

.pretty .state label:before {
    border-color: #202020 !important;
}

details {
    margin: 5px 0 20px 45px;
}